* {
   margin: 0;
   padding: 0;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
   scroll-behavior: smooth;
}

html {
   font-size: 62.5%;
}

body {
   font-family: 'Rubik', sans-serif;
   line-height: 1;
   font-weight: 400;
   background-color: transparent;
}

.wrapper {
   margin: 0 auto;
   padding: 10px 0;
   min-height: 100vh;
   background-color: #13151f;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-orient: vertical;
   -webkit-box-direction: normal;
   -ms-flex-direction: column;
   flex-direction: column;
   overflow: hidden;
}

.container {
   width: 100%;
   padding: 0 10px;
   max-width: 1190px;
   margin: 0 auto;
}

button {
   cursor: pointer;
}

a {
   text-decoration: none;
}

li {
   display: -webkit-inline-box;
   display: -ms-inline-flexbox;
   display: inline-flex;
}

:root {
   --padding-button: 6px 10px 4px 10px;
   --font-weight-extra-bold: 800;
   --font-weight-bold: 600;
   --font-size-small: 14px;
   --font-size-normal: 16px;
   --font-size-big: 18px;
   --font-size-bigger: 24px;
   --font-size-biggest: 28px;
}

@media (max-width: 992px) {
   :root {
      --font-size-small: 12px;
      --font-size-normal: 14px;
      --font-size-big: 16px;
      --font-size-bigger: 22px;
      --font-size-biggest: 26px;
   }
}

@media (max-width: 768px) {
   :root {
      --font-size-small: 12px;
      --font-size-normal: 14px;
      --font-size-big: 16px;
      --font-size-bigger: 20px;
      --font-size-biggest: 24px;
   }
}

@media (max-width: 576px) {
   :root {
      --padding-button: 3px 5px;
      --font-weight-extra-bold: 600;
      --font-weight-bold: 400;
   }
}
